<template>
  <PageContainer :title="$t('system_link')">
    <div class="flex flex-wrap">
      <a
        v-for="{ name, path } in fakeLinks"
        :key="name"
        :href="path"
        target="blank"
        class="mr-4 mb-5 underline underline-offset-1"
      >
        {{ name }}
      </a>
    </div>
  </PageContainer>
</template>
<script>
import PageContainer from "@/components/PageContainer";

export default {
  components: {
    PageContainer,
  },
  computed: {
    fakeLinks() {
      const fakeLinks = [];
      for (let i = 0; i <= 50; i++) {
        fakeLinks.push({
          name: `${this.$t("system_link")}${i}`,
          path: "http://www.google.com",
        });
      }
      return fakeLinks;
    },
  },
};
</script>
